import security from "../component/security";
import noConten from "@/assets/zwsj.png";
import loading from "@/assets/loading.gif";
export default {
  data() {
    return {
      topTitle: "提币地址",
      showXswich: false,
      menus: {
        menu1: "security"
      },
      noConten: noConten,
      loading: loading,
      isShow: true,
      DataList: []
    };
  },
  filters: {
    uppercase: function(value) {
      if (!value) {
        return "";
      }
      return value.toUpperCase();
    }
  },
  mounted() {
    this.getList();
  },
  methods: {
    getAdd() {
      this.$router.push("/wallet/addAddress");
    },
    editAddress(val) {
      var that = this;
      that.$router.push({
        name: "editAddress",
        params: {
          id: val
        }
      });
    },
    extractMoney(val) {
      var that = this;
      that.$router.push({
        name: "extractMoney",
        params: {
          id: val
        }
      });
    },
    getList() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/personal/address-list",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          if (that.$route.params.coin_type) {
            response.data.forEach(element => {
              if (element.coin_type == that.$route.params.coin_type) {
                that.DataList.push(element);
              }
            });
          } else {
            that.DataList = response.data;
          }
          that.isShow = false;
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};



